@import "variables";
@import "mixins";

html{height: 100%; font-size: 100%;font-family: @font-family;}
body{
	min-width: 1000px;
	background: @body-bg;
	height:100%;
	//min-width: 320px;
	color: @text-color;
	font: @font-weight-base @fs-base @font-family;
}
a{
	font: @font-weight-base @fs-base @font-family;
	text-decoration: none;
	//color: #1a8ce7;
	.transition(opacity, .2s, ease);
	&:hover,
	&:focus{
		opacity: .6;
	}
}
*{font-family: @font-family;font-weight: @font-weight-base;}
.out{
	height: 100%;
}
.wrapper{
	position:relative;
	z-index: 2;
	margin: 0 auto;
	min-height: 100%;
	height: auto !important;
	.clearfix();
	&:after{
		content: " ";
		height: 40px;
		width: 100%;
		display: block;
		position: relative;
	}
}
.box{
	max-width: 1240px;
	margin: 0 auto;
	padding: 0 20px;
}
.h1{
	font: @font-weight-base @font-size-h1/@line-height-h1 @font-family;
	color: @black;
	margin-bottom: 30px;
	margin-top: 20px;
	padding: 0 10px;

}
.header{
	.box{
		max-width: 100%;
		padding: 0 40px;
		.clearfix();
	}
	&-pf{
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		min-width: 1000px;
		z-index: 10;
	}
	&__top{
		background: #1a8ce7;
		min-height: 60px;
	}
	&__title{
		font-size: 1.25rem;
		color: #fff;
		float: left;
		margin: 19px 0 17px;
	}
	&__bottom{
		background: #2196f3;
	}
	&Nav{
		text-align: center;
		font-size: 0;
		line-height: 0;
		&__list{
			min-width: 936px;
			display: inline-block;
			text-align: left;
			.clearfix();
		}
		&__item{
			float: left;
			&:hover .headerNav__link,
			&.active .headerNav__link{color: rgba(255,255,255,1);opacity: 1;}
			&.active .headerNav__link:before{
				height: 2px;
				background: #bbdefb;
			}
		}
		&__link{
			position: relative;
			display: block;
			text-transform: uppercase;
			color: rgba(255,255,255,.7);
			padding: 0 16px;
			height: 45px;
			line-height: 45px;
			.transition();
			&:before{
				position: absolute;
				content: '';
				bottom: 0;
				height: 0;
				left: 0;
				right: 0;
				.transition();
			}

		}
	}
}
.slideMenu{
	position: fixed;
	width: 240px;
	left: -240px;
	top: 60px;
	bottom: 0;
	padding-top: 22px;
	z-index: 10;
	background: #f6f6f6;
	overflow: hidden;
	-webkit-transition: left .2s ease;
	transition: left .2s ease;
	&.active{
		left: 0;
		-webkit-box-shadow: 3px 3px 4px rgba(0,0,0,0.2);
		-moz-box-shadow: 3px 3px 4px rgba(0,0,0,0.2);
		box-shadow: 3px 3px 4px rgba(0,0,0,0.2);
	}
	&__in{}
	&__trigger{
		float: left;
		position: relative;
		width: 40px;
		height: 60px;
		background: #1a8ce7;
		.transition();
		&:hover{opacity: .6;}
		& > span{
			display: block;
			width: 18px;
			height: 2px;
			margin-bottom: 3px;
			&:last-child{margin-bottom: 0;}
			background: #fff;
		}
	}
	&__list{}
	&__item{}
	&__link{
		height: 50px;
		display: block;
		padding: 0 40px;
		.transition();
		&.active{
			background: #ebebeb;
			opacity: 1;
			cursor: default;
		}
	}
	&__icon{
		float: left;
		width: 40px;
		color: #777777;
		font-size: 19px;
		line-height: 44px;
		&.icon-person{
			font-size: 21px;
			line-height: 42px;
		}
	}
	&__text{
		display: block;
		overflow: hidden;
		color: #a3a3a3;
		font-size: 14px;
		font-weight: 500;
		padding: 15px 0;
	}
}
.logo{
	color: @black;
	line-height: 50px;
	float: left;
	font-size: @fs-large;  
	text-transform: uppercase;
	padding: 0 @default-margin;
}
.user{
	float: right;
	line-height: 50px;
	color: @black;
	padding: 0 @default-margin;
	&__name{
		font-size: 0.9375rem;
		color: #fff;
		font-weight: 700;
		margin: 24px 25px 0 10px;
		float: right;
	}
}
.person{
	float: right;
	padding: 0 @default-margin;
	line-height: 50px;
	margin-left: 10px;
}
.message{
	float: right;
	padding: 0 @default-margin/2;
	line-height: 50px;
}
.notification{
	float: right;
	padding: 0 @default-margin/2;
	line-height: 50px;
}
.pointer{
	cursor: pointer;
}

.content{
	clear: both;
	margin-bottom: 80px;
	position: relative;
	z-index: 3;
	.box{
		//max-width: 1280px;
		margin: auto;
	}
	.header-pf ~ &{
		margin-top: 155px;
	}
	.header-pf.header-noNav ~ &{
		margin-top: 110px;
	}
}

.tab{
	&-shadow{
		background: #fff;
		-webkit-box-shadow: 1px 1px 5px 0 rgba(0, 0, 0, 0.3);
		-moz-box-shadow: 1px 1px 5px 0 rgba(0, 0, 0, 0.3);
		box-shadow: 1px 1px 5px 0 rgba(0, 0, 0, 0.3);
		padding: 33px 40px 30px;
	}
	.heading{
		padding: 0 0 23px;
	}
	&Content{position: relative;}
}
.tabNav{
	margin: 0 0 @row-margin;
	&__list{
		overflow: hidden;
	}
	&__item{
		float: left;

		margin: 10px 20px;
		&.done{
			opacity: 1;
			cursor: default;
			.tabNav__status:before{
				opacity: 1;
				content: '\2713';
				width: 100%;
				height: 100%;
				top: 0;
				left: 0;
				font-weight: 600;
				font-size: 1.5rem;
				line-height: 8px;
				margin: 0 0 0 -1px;
			}
		}
		&.active{
			.tabNav__status:before{
				opacity: 1;
				content: '';
				height: 6px;
				width: 6px;
				top: 50%;
				left: 50%;
				margin-top: -3px;
				margin-left: -3px;
				background: @brand-primary;
				border-radius: 50%;
			}
		}

	}
	&__link{
		display: block;
		color: @brand-primary;
		font-size: @fs-base;
		text-align: center;
	}
	&__status{
		position: relative;
		display: inline-block;
		width: 20px;
		height: 20px;
		border: 3px solid @brand-primary;
		.border-radius(50%);
		&:before{
			position: absolute;
			opacity: 0;
		}
	}
	&__title{
		display: block;
	}
}
.row{
	.clearfix();
	margin: 0 0 @row-margin;
	&Box{
		overflow: hidden;
	}
	&.row-doubleMargin{
		margin: 30px 0;
	}
}
.label{
	width: 25%;
	float: left;
	min-width: 20px;
	min-height: 20px;
	line-height: 20px;
	padding: 0 20px 0 10px;
	&.label-actionRow{
		line-height: @field-size;
	}
	&.label-activate{
		text-align: left;
		line-height: @field-size;
	}
	&.label-alignLeft{
		text-align: left;
	}
	&-block{
		display: block;
		float: none;
		padding: 0 0 10px;
		width: auto;
		text-align: left;
	}
}
.fieldBox{
	float: left;
	width: 75%;
	margin-right: 20px;
	&.fieldBox100{
		width: 100%;
	}
	& + &{
		margin-top: 20px;
	}
	&Label{
		font-size: @fs-small;
		line-height: 20px;
		padding: 0 0 5px;
	}
}
.field{
	height: @field-size;
	padding: 0 30px 0 10px;
	font-size: @fs-base;
	font-family: @font-family;
	color: @black;
	width: 100%;
	border: 1px solid @field-color;
	&:focus{
		border-color: @brand-primary;
	}
	&Link{
		display: inline-block;
		font-size: @fs-small;
		color: @brand-primary;
		text-decoration: underline;
		line-height: @field-size;

		&:hover{text-decoration: none;}
	}
	&Label{
		font-size: @fs-small;
		line-height: 20px;
		padding: 0 0 5px;
	}
	&Hint{
		font-size: @fs-small;
	}
	&Helper{
		float: right;
		text-align: right;
		margin-top: 25px;
		line-height: @field-size;
		letter-spacing: -0.5px;
		width: 20%;
		white-space: nowrap;
		font-size: @fs-small;
	}
}
textarea.field{
	height: 120px;
	&.autosized{
		height: auto;
	}
}
.field_type2{
	display: block;
	height: @field-size;
	padding: 7px;
	font-size: 0.75rem;
	line-height: 1rem;
	font-family: @font-family;
	color: #191a1b;
	width: 100%;
	border: 1px solid transparent;
	background: transparent;
	.border-radius(5px);
	.transition();
	&:hover, &:focus{
		border-color: #e3e5e7;
		background: #fdfdfd;
	}
	&[disabled]{background: transparent;}
}
textarea.field_type2{
	height: 20px;
	min-height: 20px;
	&.autosized{
		height: auto;
	}
}
.field3{
	//width: 300px;
	//max-width: 100%;
	width: 100%;
	height: 40px;
	padding: 0 10px;
	font-size: @fs-base;
	font-family: @font-family;
	color: @black;
	border: 1px solid #e2e4e6;
	border-radius: 2px;
}
.select{
	height: @field-size;
	padding: 0 10px;
	font-size: @fs-base;
	font-family: @font-family;
	color: @black;
	width: 100%;
	border: 1px solid @field-color;
	&:focus{
		border-color: @brand-primary;
	}
}
.unit{
	font-size: @fs-small;
	line-height: @field-size;
	padding-left: 5px;
}
.text{
	font-weight: 400;
	font-size: @fs-small;
	margin: 0 0 5px;
	line-height: 20px;
}
.dl{
	overflow: hidden;
	font-weight: 400;
	font-size: @fs-small;
}
.dt{
	width: 195px;
	float: left;
}
.dd{
	margin: 0 0 10px 200px;
	&:last-child{
		margin-bottom: 15px;
	}
}
.tab__title{
	margin-bottom: 20px;
	padding-left: 10px;
	font-size: @fs-large;
}
.title{
	margin-bottom: 20px;
	padding: 0 20px 0 10px;
	font-size: @fs-large;
	line-height: @sm-size;
}
.table{
	width: 100%;
	&EditLink{
		background: url("../images/edit.png") 0 0 no-repeat;
		display: inline-block;
		height: 15px;
		width: 15px;
		vertical-align: middle;
		background-size: contain;
		position: absolute;
		top: 5px;
		right: 5px;
	}
	&DelLink{
		background: url("../images/trash.png") 0 0 no-repeat;
		display: inline-block;
		height: 18px;
		width: 18px;
		vertical-align: middle;
		background-size: contain;
		position: absolute;
		top: 5px;
		right: 5px;
	}
	.tr{
		.transition(background, .2s, ease);
		&:first-child:hover{background: #fff;}
		&:hover{
			background: #f6f6f6;
			cursor: pointer;
		}
	}
	.th{
		vertical-align: middle;
		height: 30px;
		text-align: left;
		font-size: @fs-small;
		padding: 5px;
		table-layout: fixed;
		max-width: 300px;
		word-wrap: break-word;
		&:first-child{
			padding-left: 10px;
		}
	}
	.td{
		vertical-align: middle;
		height: 30px;
		font-size: @fs-small;
		padding: 0 5px;
		table-layout: fixed;
		max-width: 300px;
		word-break: break-word;
		word-wrap: break-word;
		&:first-child{
			padding-left: 10px;
		}
		&__link{
			text-decoration: underline;
			font-size: @fs-small;
			color: @brand-primary;
		}
		&__link:hover{
			text-decoration: none;
			opacity: 1;
		}
	}
	.editTd{
		position: relative;
		padding-right: 40px;
	}
}
.pages{
	float: right;
	margin: 40px 0;
	&-table{
		margin: @row-margin 0;
	}
	&__box{
		overflow: hidden;
		padding: 0 20px 0 0;
	}
	&__item{
		float: left;
		position: relative;
		border-right: 2px solid @black;
		&:last-child{
			border-right: none;
		}
	}
	&__link{
		display: block;
		padding: 5px 8px;
		color: @brand-primary;
		text-decoration: underline;
	}
}
.company{
	overflow: hidden;
	margin-bottom: @sm-size;
	&__title{
		margin: 0 0 20px 130px;
		font-size: @fs-small;
		&Link{
			font-size: @fs-small;
			color: @brand-primary;
			text-decoration: underline;
		}
	}
	&__item{}
	&__label{
		width: 130px;
		white-space: nowrap;
		float: left;
		text-align: right;
		font-size: 11px;
		padding: 0 20px 0 0;
		overflow: hidden;
		clear: left;
	}
	&__value{
		margin: 0 0 9px 0;
		font-size: @fs-small;
		&Link{
			font-size: @fs-small;
			color: @brand-primary;
			text-decoration: underline;
		}
	}
	&__label + &__value{
		margin-left: 130px;
	}
}
.activateHint{
	font-size: @fs-small;
	display: inline-block;
	vertical-align: middle;
	max-width: 400px;
}
.loginForm{
	background: #ebebeb;
	max-width: 500px;
	padding: 20px;
	margin: @row-margin auto;
	.border-radius();
	.row{
		margin-bottom: 20px;
		&:last-child{
			margin-bottom: 0;
		}
	}
	.blue-btn{
		width: 100%;
		text-transform: uppercase;
	}
	.field{
		height: @field-size;
	}
}
.error{
	border-color: @error-color!important;
	&_hint{
		font-size: 0.6875rem;
		display: block;
		color: @error-color;
		padding-top: 2px;
		line-height: .7rem;
		word-break: break-word;
		word-wrap: break-word;
		&.error_hint-absolute{
			margin-top: -20px;
		}
		&.error_hint-absolute2{
			position: absolute;
			z-index: 1;
			left: 0;
			right: 0;
			top: 100%;
		}
		&.error_hint-type2{
			text-align: right;
			font-size: 1.25rem;
			line-height: 1.25rem;
		}
	}
	&.td{color: @error-color;}
	&.tr{color: @error-color;}
	&.customField{
		border-width: 2px;
	}
	&__panel{
		position: fixed!important;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 10;
		height: 80px;
		background: #b13434;
		color: #fff;
		text-align: center;
		padding: 5px 20px;
		.va_middle_out;

		&In{
			.va_middle_in;
			font-size: 13px;
			line-height: 20px;
		}
		&Icon{
			color: #fff;
			font-size: 18px;
			margin-right: 10px;
			display: inline-block;
			vertical-align: middle;
		}
		&Text{
			display: inline-block;
			vertical-align: middle;
			font-weight: 500;
			font-size: 14px;
		}
	}
	&__tooltip{
		display: none;
		position: fixed;
		z-index: 11;
		top: 100%;
		margin-top: -2px;
		right: 50px;
		width: 490px;
		background: #fff;
		padding: 10px 30px;
		-webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.25);
		-moz-box-shadow: 0 0 5px rgba(0, 0, 0, 0.25);
		box-shadow: 0 0 5px rgba(0, 0, 0, 0.25);
		color: #795454;
		font-size: 11px;
		font-style: italic;
		white-space: normal;
		line-height: 18px;
		opacity: 0;
		visibility: hidden;
		&.warning {
		  display: none;
		  position: fixed;
		  z-index: 11;
		  top: 100%;
		  margin-top: -2px;
		  right: 50px;
		  width: 490px;
		  background: #fff;
		  padding: 10px 30px;
		  -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.25);
		  -moz-box-shadow: 0 0 5px rgba(0, 0, 0, 0.25);
		  box-shadow: 0 0 5px rgba(0, 0, 0, 0.25);
		  color: #de995b ;
		  font-size: 11px;
		  font-style: italic;
		  white-space: normal;
		  line-height: 18px;
		  opacity: 0;
		  visibility: hidden;
		}
	}
}
.checkIcon{
	font-size: 19px;
	font-weight: 600;
	vertical-align: middle;
	padding-right: 5px;
}
//loading
.loading{
	position: relative;
	&:after{
		position: absolute;
		top: 50%;
		content: '';
		display: inline-block;
		vertical-align: middle;
		width: 20px;
		height: 20px;
		-webkit-animation: rotateLoader 0.6s linear infinite;
		-moz-animation: rotateLoader 0.6s linear infinite;
		-ms-animation: rotateLoader 0.6s linear infinite;
		-o-animation: rotateLoader 0.6s linear infinite;
		animation: rotateLoader 0.6s linear infinite;
		-webkit-border-radius: 100%;
		-moz-border-radius: 100%;
		border-radius: 100%;
		border-top: 1px solid #545a6a;
		border-bottom: 1px solid #d4d4db;
		border-left: 1px solid #545a6a;
		border-right: 1px solid #d4d4db;
		opacity: .3;
		margin: -10px 10px 0;
		left: auto;
		right: 0;
	}
	&-blue:after{
		width: 44px;
		height: 44px;
		border-top: 8px solid #2196f3;
		border-bottom: 8px solid transparent;
		border-left: 8px solid #2196f3;
		border-right: 8px solid transparent;
		margin: -22px 0 0 -22px;
		opacity: 1;
		left: 50%;
		right: auto;
	}
	&-wrap{
		position: relative;
	}
	&-over{
		position: fixed!important;
		content: '';
		top: 105px;
		left: 0;
		right: 0;
		bottom: 0;
		background: rgba(255, 255, 255, .8);
		z-index: 9;
		.va_middle_out;
		text-align: center;
		&In{
			.va_middle_in;
		}
	}
	&-center:after{
		left: 50%;
		margin-left: -10px;
		right: auto;
	}
	&-left:after{
		left: 0;
		right: auto;
	}
	&-specialRight:after{
		left: auto;
		right: 20%;
		margin-top: 2px;
	}
	&.btn{color: transparent!important;}
	&.btn-ok{color: transparent!important;}
	&__title{
		&Icon{
			color: #ABA6A6;
			font-size: 45px;
			line-height: 45px;
			margin-right: 15px;
			display: inline-block;
			vertical-align: middle;
		}
		&Text{
			display: inline-block;
			vertical-align: middle;
			font-size: 28px;
			line-height: 45px;
			color: #ABA6A6;
			font-weight: 400;

		}
	}
}
@keyframes rotateLoader {
	0% {
		transform: rotateZ(-360deg);
		-webkit-transform: rotateZ(-360deg);
		-moz-transform: rotateZ(-360deg);
		-o-transform: rotateZ(-360deg);
	}
	100% {
		transform: rotateZ(0deg);
		-webkit-transform: rotateZ(0deg);
		-moz-transform: rotateZ(0deg);
		-o-transform: rotateZ(0deg);
	}
}
@-webkit-keyframes rotateLoader {
	0% {
		transform: rotateZ(-360deg);
		-webkit-transform: rotateZ(-360deg);
		-moz-transform: rotateZ(-360deg);
		-o-transform: rotateZ(-360deg);
	}
	100% {
		transform: rotateZ(0deg);
		-webkit-transform: rotateZ(0deg);
		-moz-transform: rotateZ(0deg);
		-o-transform: rotateZ(0deg);
	}
}
@-moz-keyframes rotateLoader {
	0% {
		transform: rotateZ(-360deg);
		-webkit-transform: rotateZ(-360deg);
		-moz-transform: rotateZ(-360deg);
		-o-transform: rotateZ(-360deg);
	}
	100% {
		transform: rotateZ(0deg);
		-webkit-transform: rotateZ(0deg);
		-moz-transform: rotateZ(0deg);
		-o-transform: rotateZ(0deg);
	}
}
@-o-keyframes rotateLoader {
	0% {
		transform: rotateZ(-360deg);
		-webkit-transform: rotateZ(-360deg);
