//cssload-jumping
.cssload-jumping{
	.va_middle_in;
	position:relative;
	margin: 25px auto 0;
	width: 107px;
	span{
		display: inline-block;
		height: 15px;
		width: 15px;
		background: #2196f3;
		border-radius: 487px;
		background-clip: padding-box;
		-o-background-clip: padding-box;
		-ms-background-clip: padding-box;
		-webkit-background-clip: padding-box;
		-moz-background-clip: padding-box;
		&:nth-child(1){
			-webkit-animation: cssload-jumpingScale 1.15s 0.12s infinite cubic-bezier(0.6, -0.28, 0.735, 0.045);
			animation: cssload-jumpingScale 1.15s 0.12s infinite cubic-bezier(0.6, -0.28, 0.735, 0.045);
		}
		&:nth-child(2){
			-webkit-animation: cssload-jumpingScale 1.15s 0.23s infinite cubic-bezier(0.6, -0.28, 0.735, 0.045);
			animation: cssload-jumpingScale 1.15s 0.23s infinite cubic-bezier(0.6, -0.28, 0.735, 0.045);
		}
		&:nth-child(3){
			-webkit-animation: cssload-jumpingScale 1.15s 0.35s infinite cubic-bezier(0.6, -0.28, 0.735, 0.045);
			animation: cssload-jumpingScale 1.15s 0.35s infinite cubic-bezier(0.6, -0.28, 0.735, 0.045);
		}
		&:nth-child(4){
			-webkit-animation: cssload-jumpingScale 1.15s 0.46s infinite cubic-bezier(0.6, -0.28, 0.735, 0.045);
			animation: cssload-jumpingScale 1.15s 0.46s infinite cubic-bezier(0.6, -0.28, 0.735, 0.045);
		}
		&:nth-child(5){
			-webkit-animation: cssload-jumpingScale 1.15s 0.58s infinite cubic-bezier(0.6, -0.28, 0.735, 0.045);
			animation: cssload-jumpingScale 1.15s 0.58s infinite cubic-bezier(0.6, -0.28, 0.735, 0.045);
		}
	}
}
@keyframes cssload-jumpingScale {
	0% {
		transform: scale(0);
	}
	25% {
		transform: scale(0.9, 0.9);
		background: #b4d9f7;
	}
	50% {
		transform: scale(1, 1);
		margin: 0 3px;
		background: #1a8ce7;
	}
	100% {
		transform: scale(0);
	}
}
@-webkit-keyframes cssload-jumpingScale {
	0% {
		-webkit-transform: scale(0);
	}
	25% {
		-webkit-transform: scale(0.9, 0.9);
		background: #b4d9f7;
	}
	50% {
		-webkit-transform: scale(1, 1);
		margin: 0 3px;
		background: #1a8ce7;
	}
	100% {
		-webkit-transform: scale(0);
	}
}
//progress
.progress{
	display: inline-block;
	vertical-align: middle;
	width: 200px;
	height: 20px;
	margin: 0 10px;
	overflow: hidden;
	background-color: #f5f5f5;
	border-radius: 4px;
	-webkit-box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);
	box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);

	&-bar{
		float: left;
		width: 0;
		height: 100%;
		font-size: 12px;
		line-height: 20px;
		max-width: 100%;
		color: #fff;
		text-align: center;
		background-color: #337ab7;
		-webkit-box-shadow: inset 0 -1px 0 rgba(0,0,0,.15);
		box-shadow: inset 0 -1px 0 rgba(0,0,0,.15);
		-webkit-transition: width .6s ease;
		-o-transition: width .6s ease;
		transition: width .6s ease;

		background-image: -webkit-linear-gradient(45deg,rgba(255,255,255,.15) 25%,transparent 25%,transparent 50%,rgba(255,255,255,.15) 50%,rgba(255,255,255,.15) 75%,transparent 75%,transparent);
		background-image: -o-linear-gradient(45deg,rgba(255,255,255,.15) 25%,transparent 25%,transparent 50%,rgba(255,255,255,.15) 50%,rgba(255,255,255,.15) 75%,transparent 75%,transparent);
		background-image: linear-gradient(45deg,rgba(255,255,255,.15) 25%,transparent 25%,transparent 50%,rgba(255,255,255,.15) 50%,rgba(255,255,255,.15) 75%,transparent 75%,transparent);
		-webkit-background-size: 40px 40px;
		background-size: 40px 40px;
		-webkit-animation: progress-bar-stripes 2s linear infinite;
		-o-animation: progress-bar-stripes 2s linear infinite;
		animation: progress-bar-stripes 2s linear infinite;
	}
}
@-webkit-keyframes progress-bar-stripes {
	from {background-position: 40px 0;}
	to {background-position: 0 0;}
}
@keyframes progress-bar-stripes {
	from {background-position: 40px 0;}
	to {background-position: 0 0;}
}
//logoLoading
.logoLoading{
	-webkit-animation-name: logoLoading;
	animation-name: logoLoading;
	-webkit-animation-duration: 1.5s;
	animation-duration: 1.5s;
	-webkit-animation-fill-mode: forwards;
	animation-fill-mode: forwards;
	-webkit-animation-iteration-count: infinite;
	animation-iteration-count: infinite;
	.va_middle_in();
	&__box{
		.va_middle_out();
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		z-index: 3;
		text-align: center;
	}
}
@-webkit-keyframes logoLoading {
	0% {
		-webkit-transform: scale3d(1, 1, 1) rotateY(0deg);
		transform: scale3d(1, 1, 1) rotateY(0deg);
	}
	50% {
		-webkit-transform: scale3d(1, 1, 1) rotateY(180deg);
		transform: scale3d(1, 1, 1) rotateY(180deg);
	}
	100% {
		-webkit-transform: scale3d(1, 1, 1) rotateY(0deg);
		transform: scale3d(1, 1, 1) rotateY(0deg);
	}

}
@keyframes logoLoading {
	0% {
		-webkit-transform: scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg);
		transform: scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg);
	}
	50% {
		-webkit-transform: scale3d(1, 1, 1) rotateY(180deg);
		transform: scale3d(1, 1, 1) rotateY(180deg);
	}
	100% {
		-webkit-transform: scale3d(1, 1, 1) rotateY(0deg);
		transform: scale3d(1, 1, 1) rotateY(0deg);
	}
}

