.checkbox {
	position: relative;
	margin: 0 1rem 0 0 ;
	cursor: pointer;
	&:before {
		.transition(all, .3s, ease-in-out);
		content: "";
		position: absolute;
		left: 0;
		z-index: 1;
		width: 1rem;
		height: 1rem;
		border: 2px solid #f2f2f2;
	}

	&:checked {
		&:before {
			.transform(rotate(-45deg));
			height: .5rem;
			border-color: @brand-green;
			border-top-style: none;
			border-right-style: none;
		}
	}

	&:after {
		content: "";
		position: absolute;
		top: rem(-2);
		left: 0;
		width: 1.1rem;
		height: 1.1rem;
		background: #fff;
		cursor: pointer;
	}
}
.label--checkbox {
	position: relative;
	margin: 0 2px;
	font-family: Arial, sans-serif;
	line-height: 1;
	cursor: pointer;
}
.text--checkbox{
	//width: 155px;
	float: left;
	color: #a7a7a7;
	//padding-right: 40px;
	padding-right: 10px;
}


.md-checkboxInput{
	opacity: 0;
	min-width: 20px;
	min-height: 20px;
	float: right;
	& ~ *{
		cursor: pointer;
	}
	&:focus{
		&:not([disabled]) ~ ._md-container:before {
			left: -8px;
			top: -8px;
			right: -8px;
			bottom: -8px;
			background-color: rgba(0,0,0,.12);
		}
		&:not([disabled]) ~ ._md-container ._md-icon{
			color: transparent;
		}
		&:checked:not([disabled]) ~ ._md-container:before {
			background: #34b162;
			opacity: .2;
		}
	}
	&:checked ~ ._md-container ._md-icon {
		border: none;
		background-color: #34b162;
	}
	&:checked ~ ._md-container ._md-icon:after {
		color: rgba(255,255,255,0.87);
	}
	&:checked ~ .md-ink-ripple {
		color: #34b162;
	}
	
	&[disabled] ~ *{
		cursor: default;
	}
	&:checked[disabled]~ ._md-icon {
		background-color: rgba(0,0,0,0.38);
	}
}
.md-checkbox {
	display: inline-block;
	white-space: nowrap;
	//cursor: pointer;
	outline: 0;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	position: relative;
	min-width: 20px;
	//min-height: 20px;
	margin-left: 0;
	font-size: 0;
	line-height: 0;
}

.md-checkbox ._md-container {
	position: absolute;
	top: 50%;
	-webkit-transform: translateY(-50%);
	transform: translateY(-50%);
	display: inline-block;
	width: 20px;
	height: 20px;
	right: 0;
	&:before {
		background-color: transparent;
		border-radius: 50%;
		content: '';
		position: absolute;
		display: block;
		height: auto;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		transition: all .1s;
		width: auto;
	}
	&:after {
		box-sizing: border-box;
		content: '';
		position: absolute;
		top: -10px;
		right: -10px;
		bottom: -10px;
		left: -10px;
	}
}
.md-checkbox ._md-container .md-ripple-container {
	position: absolute;
	display: block;
	width: auto;
	height: auto;
	left: -15px;
	top: -15px;
	right: -15px;
	bottom: -15px;
}
.md-checkbox ._md-icon {
	//transition: all .5s;
	transition: 240ms;
	position: absolute;
	top: 0;
	left: 0;
	width: 20px;
	height: 20px;
	border-radius: 2px;
	border: 2px solid rgba(0,0,0,0.54);
	color: #fff;
	&:after {
		-webkit-transform: rotate(45deg);
		transform: rotate(45deg);
		position: absolute;
		left: 50%;
		top: 50%;
		display: table;
		width: 6.67px;
		height: 13.33px;
		border-width: 2px;
		border-style: solid;
		border-top: 0;
		border-left: 0;
		content: '';
		margin-top: -8px;
		margin-left: -2px;
	}
}
.md-checkbox ._md-label {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	white-space: normal;
	-webkit-user-select: text;
	-moz-user-select: text;
	-ms-user-select: text;
	user-select: text;

	float: left;
	color: #a7a7a7;
	margin-right: 15px;
	font-size: 0.8125rem;
	line-height: 1.25rem;
}

.type2{
	.md-checkboxInput:checked:not([disabled]) ~ ._md-container:before {
		background: @brand-red;
	}
	.md-checkboxInput:checked ~ ._md-container ._md-icon{
		background: @brand-red;
	}
}

